import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./asset-type-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class AssetTypeAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;

 
assetTypeForm: FormGroup;
contractassettypeId = 0;  
ajaxData : AssetTypeData[];
contractassettype= new AssetTypeData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.contractassettypeId).subscribe(params=>{
			if(params.contractassettypeId != undefined){
				this.contractassettypeId=params.contractassettypeId; 
			}
		}); 
	console.log("contractassettypeId=="+this.contractassettypeId);
	if(this.contractassettypeId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.assetTypeForm = new FormGroup({
			'contractassettypeId' : new FormControl(''),
			'contractassettypeName' : new FormControl('', [ Validators.required]),
			'contractassettypeEntryId' : new FormControl(''),
			'contractassettypeEntryDate' : new FormControl(''),
			'contractassettypeModifiedId' : new FormControl(''),
			'contractassettypeModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get contractassettypeName(){ return this.assetTypeForm.get('contractassettypeName')};
	    get contractassettypeEntryId(){ return this.assetTypeForm.get('contractassettypeEntryId')};
	    get contractassettypeEntryDate(){ return this.assetTypeForm.get('contractassettypeEntryDate')};
	    get contractassettypeModifiedId(){ return this.assetTypeForm.get('contractassettypeModifiedId')};
	    get contractassettypeModifiedDate(){ return this.assetTypeForm.get('contractassettypeModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<AssetTypeData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/assettype-data ',
							 			JSON.stringify({ 'contractassettypeId' :this.contractassettypeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.contractassettype = data['populateData'][0];
																		console.log("contractassettypeModifiedId=="+this.contractassettype.contractassettypeModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['service/asset-type-list']);     
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.contractassettype[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.assetTypeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.contractassettypeId == 0){ 
				this.contractassettype['requestType'] = 'add';
			}else{
				this.contractassettype['requestType'] = 'update';
				this.contractassettype['contractassettypeId'] = this.contractassettypeId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/assettype',
									JSON.stringify(this.contractassettype),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.assetTypeForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.contractassettype);
	  }
 
  onDelete(){
		 this.contractassettype['requestType'] = 'delete';
		this.contractassettype['contractassettypeId'] = this.contractassettypeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/assettype',
									JSON.stringify(this.contractassettype),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.assetTypeForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class  AssetTypeData {
    contractassettypeId: string;
   	contractassettypeName: string;
	contractassettypeEntryId: string;
	contractassettypeEntryBy: string;
	contractassettypeEntryDate: string;
	contractassettypeModifiedId: string;
	contractassettypeModifiedBy: string;
	contractassettypeModifiedDate: string;
  
}


